<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('ProductAvailability') }}
                        </h5>
                        <!-- {{ LocationDataTree }} -->
                        <button v-on:click="ClosePOPuP(event)" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>                            
                    </div>
                    <div class="modal-body">                                
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">

                            <div class="row">
                                <div class="col-12">
                                    <div class="rad_value" type="radiobutton">
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" class="custom-control-input dynamic IssuedByIds" id="SearchProductName" @change="searchByProduct('barcode')"  :checked="SearchByBarcode"  name="SearchProduct" value="CheckedCheckBox">                                                
                                            <label class="custom-control-label" for="SearchProductName">{{$t('SearchbyBarcodeRFID')}}</label>
                                            
                                        </div>
                                        <div class="custom-control custom-radio custom-control-inline">
                                            <input type="radio" class="custom-control-input dynamic IssuedByIds" id="SearchProducts" @change="searchByProduct('product')"   :checked="!SearchByBarcode"  name="SearchProduct" value="CheckedCheckBox">
                                            <label class="custom-control-label" for="SearchProducts">{{$t('SearchbyProduct')}}</label>
                                        </div>
                                    </div>
                                </div>
                                <div id="searchByBarcode" class="col-12 mt-3 p-0">
                                    <div class="col-12 col-sm-9 col-md-9 col-lg-10 float-left" v-if="SearchByBarcode">
                                        <div class="form-group mb-2">
                                            <label><span id="">{{$t('SearchbyBarcodeRFID')}}</span></label><span class="text-danger">*</span>
                                            <input id="txtBarcode"  name="txtBarcode" v-model="txtBarcode" placeholder="Enter Barcode" value="" class="form-control" type="text">
                                        </div>
                                    </div>
                                    <div class="col-12 col-sm-3 col-md-3 col-lg-2 float-left pl-md-0 mt-0 mt-md-4 mt-sm-4" v-if="SearchByBarcode">
                                        <div class="background-0 text-left w-100 form-check-inline">
                                            <input type="hidden" id="hdnSearch" value="">
                                            <a title="" id="Searchassest_barcode" href="javascript:;" v-on:click="SearchassestBarcode" class="btn btn-success w-50 mr-1 px-4 hlsearch" data-toggle-tooltip="tooltip" data-original-title="Search"><em class="fa fa-search"></em></a>
                                            <a id="aClear" href="javascript:;" title="Clear all " v-on:click="CleareAssestBarcode" class="btn btn-danger w-50 clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><em class="fa fa-refresh"></em></a>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12 float-left searchByProduct" v-if="!SearchByBarcode">
                                    <div class="form-group">
                                        <label for="Subject">Location:<span class="text-danger">*</span></label>
                                        <!-- <select  v-bind:class="{ 'form-control': true}" name="location" v-on:change="GetddlProduct($event)" v-model="location_id" >
                                                    <option value="">Select</option>
                                                    <option v-for="(item,index) in locationDDLData"  :key="index" v-bind:value="item.value">{{item.name}}</option>
                                        </select> -->
                                        <Field name="Location" :rules="{ 'required': true }" v-slot="{ errors }"> 
                                                <tree-view 
                                                   v-model="location_id" 
                                                   :options="options" 
                                                   :search-change="GetddlProduct(location_id)"
                                                   placeholder="Select Location"
                                                   :show-count="true"
                                                   v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"                                                 
                                                   >
                                                </tree-view>
                                                <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{
                                                errors[0] }}</span>
                                         </Field>
                                    </div>
                                </div>
                                <div id="searchByProduct" v-if="!SearchByBarcode" class="col-12 float-left">
                                    <div class="col-12 float-left p-0">
                                        <div class="rad_value" type="radiobutton">
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic"  id="ProductNameItem"  @change=" searchByItemName('ProductName')"  :checked="IsProductName" value="ProductName">
                                                <label class="custom-control-label" for="ProductNameItem">{{$t('AssetId')}}</label>
                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" class="custom-control-input dynamic" id="ItemName" @change="searchByItemName('Product')"  :checked="!IsProductName" value="Product" >
                                                <label class="custom-control-label" for="ItemName">{{$t('ItemName')}}</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-12 float-left mt-3 p-0 productName" v-if="IsProductName">
                                        <div class="form-group">
                                        <label for="Subject">{{$t('AssetId')}}:<span class="text-danger">*</span></label>
                                        <Field name="productName" :rules="{ 'required': true }" v-slot="{ errors }"> 
                                            <select  
                                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" name="productName" v-model="Product_Id" >
                                                <option value="">Select</option>
                                                <option v-for="(item,iindex) in productDDLData"  :key="iindex" v-bind:value="item.value">{{item.name}}</option>
                                            </select>
                                            <span v-if="errors[0]" name="productName" class="invalid-feedback d-block">{{
                                                errors[0] }}</span>
                                        </Field>
                                    </div>
                                    </div>
                                    <div class="col-12 float-left mt-3 p-0 product" v-if="!IsProductName">
                                        <div class="form-group">
                                            <label>Item Name:<span class="text-danger">*</span></label>
                                            <div class="input-group mb-3" style="position: relative;">
                                                <input id="txtAssetItems" name="txtAssetItems" v-on:input="GetAssetUniqueData" v-model="assetUniqueItem" value="iselectVal" placeholder="Enter Product Name" class="form-control" type="text" autocomplete="off">
                                                <div class="unique_dynamicdatalist">
                                                    <ul class="listOfProduct" v-if="IsDDLShowHide">
                                                        <li class="listitem all-items" v-for="(item,index) in assetUniqueData" v-on:click="uniqueItemChange(item)" :key="index" >{{item.name}}</li>
                                                    </ul>
                                                </div>
                                                <a class="btn float-left" href="javascript:;" v-on:click="itemDDLShowHide" 
                                                    style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;" id="customddlinput">
                                                    <em class="dropdown-toggle float-right"></em>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12 mb-3 mt-3">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span>{{$t('AvailableBetween')}} </span>
                                        </h5>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">
                               <div class="input-group">
                                    <label>From:</label><span class="text-danger">*</span>
                                    <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="fromDate"
                                        :minute-increment="5" class="timedate  input-group" v-model="fromDate">
                                        <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                            <input placeholder="M/D/YYYY h:mm A" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                            <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                        </template>
                                    </v-date-picker>                                
                                 </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">
                                    <div class="form-group">
                                            <label >{{$t('EventStartTime')}}</label>
                                            <div class="input-group">                                                   
                                                <v-date-picker  mode="Time"  class="input-group" v-model="StartTime" @input="resetAvailabilityStatus">
                                                    <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                        <input class="form-control"  :value="inputValue" v-on="inputEvents"  />
                                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-clock-o"></em></span></div>
                                                    </template>
                                                </v-date-picker>                                                   
                                            </div>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">
                                    <div class="input-group">
                                <label>Till </label><span class="text-danger">*</span>
                                <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="toDate"
                                    :minute-increment="5" class="timedate  input-group" v-model="toDate">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="M/D/YYYY h:mm A" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                    </template>
                                </v-date-picker>                                
                            </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-xl-6">                                    
                                    <div class="form-group">
                                        <label >{{$t('CAEndTime')}}</label>
                                        <div class="input-group">                                                   
                                            <v-date-picker  mode="Time"  class="input-group" v-model="EndTime"  @input="resetAvailabilityStatus">
                                                <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                    <input class="form-control"  :value="inputValue" v-on="inputEvents" />
                                                    <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-clock-o"></em></span></div>
                                                </template>
                                            </v-date-picker>                                                   
                                        </div>
                                    </div>
                                </div>                               
                                <CheckBarCodeAvalibility v-if="tblassestgroupinfoIsShow" :BarCodeData="BarCodeData" :RequestFrom="RequestFrom"></CheckBarCodeAvalibility>
                                <AvailabilityStatus v-if="divAvailabilityStatusIsShow" :GetUniqueProductIdData="GetUniqueProductIdData" :noOfAvailableItems="noOfAvailableItems" 
                                    :statusAvailable="statusAvailable" :uniqueItems="uniqueItems" :statusReserved="statusReserved" :statusUnAvailable="statusUnAvailable" 
                                    :divAvailabilityStatus="divAvailabilityStatusIsShow" @onUniqueItemClick="onUniqueItemClick($event)" >
                                </AvailabilityStatus>                                
                                <div class="col-12 mt-3  flex-row-reverse mt-3">
                                    <div class="col-lg-12 text-left">
                                        <div class="py-2">
                                            <span id="spnSelfCheckOutError" class="text-danger"> </span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12 text-right mt-3">
                                    <a href="javascript:;" @click="ProceedToAction('reserve');" class="btn btn-warning" v-if="btnReserve" >{{$t('ProceedtoReserve')}}</a>
                                    <a href="javascript:;" @click="checkAssetAvailability" class="btn btn-success" v-if="btnCheckAvailability">{{$t('Check_Availability')}}</a>
                                    <a href="javascript:;" @click="ProceedToAction('checkout');" class="btn btn-success" v-if="btnCheckout" >{{$t('ProceedtoCheckout')}}</a>
                                    <a href="javascript:;" @click="ProceedToAction('assign');" class="btn btn-primary" v-if="btnAssign" >{{$t('ProceedtoAssign')}}</a>                                
                                </div>
                                <div class="col-12 mt-3  flex-row-reverse mt-3">
                                    <div class="col-lg-12 text-left">
                                        <div class="py-2">
                                            <span id="spnSelfCheckOutError" class="text-danger">{{ErrorText}}</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import CheckavailablityDataService from '../../service/DataService'
import CheckBarCodeAvalibility from '../../components/Modal/CheckBarCodeAvalibility.vue'
import AvailabilityStatus from '../../components/Modal/AvailabilityStatus.vue'
import Treeselect from '@riophae/vue-treeselect' 
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default{   
    components:{
        CheckBarCodeAvalibility,AvailabilityStatus,'tree-view':Treeselect
    },
    data(){
        return{
            productDDLData:[],
            locationDDLData:[],  
            assetUniqueData:[], 
            checkAvailabilityList:[],
            location_id:null,
            SearchByBarcode:true,
            IsProductName:true,
            IsDDLShowHide:false,
            assetUniqueItem:'',
            txtBarcode:'',
            tblassestgroupinfoIsShow:false,
            divAvailabilityStatusIsShow:false,
            popover: {visibility: 'click'},
            isLoading:false,
            BarCodeData:[],
            RequestFrom:'',
            IsShowButton:true,
            CheckedCheckBox:'SearchProductName',
            BarcodeSelected:'BarCodeSearch',
            StartTime:new Date(),
            EndTime:new Date(),
            UniqueNameListing:1,
            IsProduct:'ProductName',
            Product_Id:null,
            fromDate:new Date(),
            toDate:new Date(),
            GetUniqueProductIdData:[],
            SpnSelfCheckOutError:'',
            ErrorText:'',
            ASSETUniqueId:null,
            noOfAvailableItems:null,
            statusAvailable:false,
            uniqueItems:true,
            statusReserved:false,
            statusUnAvailable:false,
            btnCheckAvailability:true,
            btnCheckout:false,
            btnAssign:false,
            btnReserve:false,
            IsDefault:null,
            UserType:null,
            module_id:null,
            GetKeyData:[],
            isUserSelfCheckoutEnabled:false,
            iselectVal:null,
            CheckeduniqueIds:null,
            LocationName:'',
            uniqueProductName:'',
            hdnQuantity:null,
            LocationDataTree:[]
        }
    },
    created:function(){   
        debugger      
        var vm=this;
        vm.GetddlLocation(); 
        vm.searchByProduct('barcode');
        vm.fromDate = vm.$options.filters.formatDate(vm.fromDate);
        vm.toDate = vm.$options.filters.formatDate(vm.toDate);         
        vm.UniqueNameListing = vm.BarcodeSelected != 'BarCodeSearch'?vm.UniqueNameListing=1 : vm.UniqueNameListing=0;   
        vm.IsDefault = vm.GetUserInfo.IsDefault;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.GetKey();      
    },
    methods:{ 
        GetKey:async function(){            
            var vm=this;
            await vm.GetModuleByName();            
            var url=`moduleId=${vm.module_id}&code=USR`;
            await CheckavailablityDataService.GetKeys(url).then(response=>{ 
                if(response.data.length>0){                    
                    vm.GetKeyData=response.data;
                    vm.GetKeyData.filter(item=>{
                        if(item.CORE_CONFIG_KEY =='SelfCheckout' || item.CORE_CONFIG_KEY=='SelfTransfer'){
                           return vm.isUserSelfCheckoutEnabled=true;
                        }
                    });
                }
                else{
                    vm.isUserSelfCheckoutEnabled=false;
                } 
            });
        },
        GetModuleByName:async function(ths){            
            var url=`moduleName=Asset`;
            await CheckavailablityDataService.GetModuleByName(url).then(response=>{                
                this.module_id=response.data[0].module_id;
            });
        },
        GetAssetAvailability:async function(){ 
            debugger           
            var vm=this;
            var assetCatalog='';
            var assestUniqueId='';           
            if (vm.BarcodeSelected == 'BarCodeSearch') {
                 assetCatalog = vm.BarCodeData[0].AssetCatalogueId;
                 assestUniqueId = vm.BarCodeData[0].ParentUniqueId;
            } else {
                 assetCatalog = vm.Product_Id;
                 assestUniqueId = vm.iselectVal;
            }
            var assignedFrom = vm.$options.filters.formatDate(vm.fromDate);
            var assignedTill = vm.$options.filters.formatDate(vm.toDate);
            var startTime = vm.$options.filters.formatTime(vm.StartTime);
            var endTime = vm.$options.filters.formatTime(vm.EndTime);
            var startDate = new Date(Date.parse(assignedFrom + ' ' + startTime));
            var endDate = new Date(Date.parse(assignedTill + ' ' + endTime));
            if (startDate >= endDate) {
                vm.ShowAlert(vm.$t("TimeRangeValidation"), "failure", vm.$t('Alert'));
                return false;
            }
            if ((assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0) && (assetCatalog == null || assetCatalog == '' || assetCatalog == 0)) {
                if ((assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0)) {                    
                    vm.ShowAlert(vm.$t("ProductRequired"), "failure", vm.$t('Alert'));
                }
                else {                    
                    vm.ShowAlert(vm.$t("InvalidProduct"), "failure", vm.$t('Alert'));                    
                }
            }
            else{
                vm.isLoading=true;
                if(vm.BarcodeSelected == 'BarCodeSearch' && vm.UniqueNameListing==0){
                    var url=`assetUniqueId=${assestUniqueId}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=&type=Product`;
                    await CheckavailablityDataService.GetAssetAvailability(url).then(response=>{                             
                        if (response.data.availability != 'ERROR') {
                                if (response.data.availability.length > 0) {
                                    if (response.data.availability[0].availabilty == 'AVAILABLE') {
                                        var obj = { 
                                            status: 'available', 
                                            AvailableItems: response.data.availabilty[0].availablE_QUANTITY, 
                                            IS_SELF_CHECKOUT: response.data.availabilty[0].iS_SELF_CHECKOUT 
                                        };
                                        vm.DisplayAvailability(obj);
                                    }
                                    else if (response.data.availabilty[0].availabilty == 'RESERVED') {
                                        var obj = { status: 'reserved' };
                                        vm.DisplayAvailability(obj);
                                    }
                                    else {
                                        vm.DisplayAvailability('unAvailable');
                                    }
                                   // $('#divCalendar').data({ 'assetUniqueId': assestUniqueId, 'assignedFrom': assignedFrom, 'assignedTill': assignedTill, 'startTime': startTime, 'endTime': endTime, 'locationId': $("#hdnAssetLocationId").val(), type: 'Product' });
                                }else{
                                    vm.DisplayAvailability('unAvailable');
                                }
                            }else{
                                vm.DisplayAvailability('unAvailable');
                            }
                            vm.isLoading=false;                       
                    });
                }
                else{                    
                    if(vm.IsProduct=='ProductName'){
                        if(vm.BarcodeSelected == 'BarCodeSearch'){
                            if (vm.location_id == "" && vm.location_id==0) {                               
                                vm.ShowAlert(vm.$t("CostCentreReq"), "failure", vm.$t('Alert'));
                                vm.isLoading=false;
                                return false;
                            }
                        }else{                            
                            await vm.GetUniqueProductId();
                            assestUniqueId=vm.ASSETUniqueId;                            
                        }                    
                    }                    
                    if (!(assestUniqueId == null || assestUniqueId == '' || assestUniqueId == 0 || vm.IsProduct=='Product')) {
                        var url=`assetUniqueId=${assetCatalog}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${vm.location_id}&type=ProductName`;
                        await CheckavailablityDataService.GetAssetAvailability(url).then(response=>{                                                        
                                if (response.data.availabilty != 'ERROR') {
                                    if (response.data.availability.length > 0) {
                                        if (response.data.availability[0].availabilty == 'AVAILABLE') {                                            
                                            var obj = { 
                                                status: 'available', 
                                                AvailableItems: response.data.availability[0].availablE_QUANTITY, 
                                                UNIQUE_ITEMS: response.data.availability[0].uniquE_ITEMS, 
                                                IS_SELF_CHECKOUT: response.data.availability[0].iS_SELF_CHECKOUT
                                            };
                                            vm.DisplayAvailability(obj);
                                        }
                                        else if (response.data.availabilty[0].availabilty == 'RESERVED') {
                                            var obj = { status: 'reserved' };
                                            vm.DisplayAvailability(obj);
                                        }
                                        else {
                                            vm.DisplayAvailability('unAvailable');
                                        }
                                        // $('#divCalendar').data({ 'assetUniqueId': assestUniqueId, 'assignedFrom': assignedFrom, 'assignedTill': assignedTill, 'startTime': startTime, 'endTime': endTime, 'locationId': $("#hdnAssetLocationId").val(), type: 'Product' });
                                    }
                                    else{
                                        vm.DisplayAvailability('unAvailable');
                                    }
                                }else{
                                    vm.DisplayAvailability('unAvailable');
                                }
                                vm.isLoading=false;                           
                        });
                    }
                    else{                        
                        var url=`assetUniqueId=${assestUniqueId}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${vm.location_id}&type=Product`;
                        await CheckavailablityDataService.GetAssetAvailability(url).then(response=>{                                                           
                                if (response.data.availability != 'ERROR') {
                                    if (response.data.availability.length > 0) {
                                        if (response.data.availability[0].availability == 'AVAILABLE') {
                                            var obj = { 
                                                status: 'available', 
                                                AvailableItems: response.data.availability[0].availablE_QUANTITY, 
                                                IS_SELF_CHECKOUT: response.data.availability[0].iS_SELF_CHECKOUT
                                            };
                                            vm.DisplayAvailability(obj);
                                        }
                                        else if (response.data.availability[0].AVAILABILTY == 'RESERVED') {
                                            var obj = { status: 'reserved' };
                                            vm.DisplayAvailability(obj);
                                        }
                                        else {
                                            vm.DisplayAvailability('unAvailable');
                                        }
                                        // $('#divCalendar').data({ 'assetUniqueId': assestUniqueId, 'assignedFrom': assignedFrom, 'assignedTill': assignedTill, 'startTime': startTime, 'endTime': endTime, 'locationId': $("#hdnCompanyLocationSelect").val(), type:'Product' });
                                    }
                                    else {
                                        vm.DisplayAvailability('unAvailable');
                                    }
                                }
                                else {
                                    vm.DisplayAvailability('unAvailable');
                                } 
                                vm.isLoading=false;                                                 
                        });
                    }
                }
            }
        },
        DisplayAvailability:function(availabilty){                       
            var vm =this;
            vm.divAvailabilityStatusIsShow=true;
            if (availabilty.status) {                
                vm.statusAvailable=true;
                if (availabilty.status == 'available') {
                    if ((vm.IsProduct == "ProductName") && (vm.BarcodeSelected != 'BarCodeSearch' && vm.UniqueNameListing != 1)) {
                        vm.noOfAvailableItems=availabilty.AvailableItems;
                    }
                    if (availabilty.UNIQUE_ITEMS) {
                        vm.GetUniqueProductIdData=JSON.parse(availabilty.UNIQUE_ITEMS);                        
                    }else{
                        vm.uniqueItems=true;
                    }
                    var assignedFrom = vm.fromDate;
                    var startTime = vm.StartTime;
                    var startDate = new Date(new Date(Date.parse(assignedFrom)).setHours(0, 0, 0, 0));                  
                    var startDateTime=assignedFrom + ' ' + startTime.getTime()/60000;
                    startDateTime=startDateTime.split(' ');
                    startDateTime=startDateTime[1];                    
                    var date = new Date(new Date().setHours(0, 0, 0, 0));                    
                    vm.hdnQuantity=availabilty.AvailableItems;
                    vm.statusReserved=false;
                    vm.statusUnAvailable=false;
                    vm.btnCheckAvailability=false;
                    vm.btnCheckout=false;
                    vm.btnAssign=false;
                    if (+startDate === +date) {
                        if (vm.UserType == 'CA' && `${vm.IsDefault}`==1) {
                            vm.btnCheckout=true;
                            vm.btnAssign=true;
                        }
                        else{
                            if (vm.isUserSelfCheckoutEnabled == 'True') {
                                vm.btnAssign=true;
                            }
                            if (vm.isUserSelfCheckoutEnabled == true && availabilty.IS_SELF_CHECKOUT == true) {
                                vm.btnCheckout=true;
                            }
                            else if(availabilty.IS_SELF_CHECKOUT == false){
                                vm.ErrorText='Self checkout is not enabled for this product.';
                            }
                            else{
                                if (vm.UserType == 'CA' ){
                                    vm.ErrorText='Please enable your self checkout option under user configuration.';
                                }
                                else{
                                    vm.ErrorText='Please ask the company admin to enable your self checkout option under user configuration.';
                                }
                            }
                        }
                        if (startDateTime - (new Date().getTime() / 60000) >= 5) {
                           vm.btnReserve=true;
                        }
                    }
                    if (startDate > date) {                        
                        vm.btnReserve=true;
                        if(vm.UserType == 'CA' && vm.IsDefault==true) {
                            vm.btnAssign=true;
                        }
                    }
                }
                else if (availabilty.status == 'reserved') {
                    vm.noOfAvailableItems=null;
                    vm.statusReserved=true;            
                    vm.statusUnAvailable=false;                 
