<template>
    <div class="col-12 mt-3 listing tblassestgroupinfo">
        <div class="table-responsive">
            <table id="tblassestgroupinfo" class="table table-bordered dt-responsive nowrap mt-0">
                <thead class="thead-bg">
                    <tr>
                        <th style="max-width:120px;" class="text-center"><span>{{$t('SerialNumber')}}</span></th>
                        <th><span>{{$t('PRODUCT_NAME')}}</span></th>
                        <th><span>{{$t('Location')}}</span></th>
                    </tr>
                </thead>
                <tbody v-for="(item,index) in BarCodeData" :key="index">
                    <template v-if="item.CreateUniqueNameListing">
                        <tr class="showassetcataloguegroup" :class="{'insertedrow bg-gold':item.AssetCatalogueGroup.length>0}">
                            <td :style='containercolor' class="textaligncenter align-middle text-center">
                                <!-- <span v-if="item.AssetCatalogueGroup.length>0" class='togglearrow arrowwhitecollapse_roles fa fa-caret-right fa-2x showcatalog showassetcatalog'>{{ index }}</span>
                                <span v-else class='showcatalog hidden showassetcatalog' >{{ index }}</span> -->
                                {{ index + 1 }}
                            </td>
                            <td class="align-middle ad">{{ item.AssetCatalogName }}</td>
                            <td class="align-middle ad">
                                <template v-if="BarCodeData.length==1">
                                    <select  v-bind:class="{ 'form-control disabled': true}" name="location">
                                        <option  v-bind:value="item.LocationId">{{item.LocationName}}</option>
                                    </select>
                                </template>
                                <template v-else>
                                    <select  v-bind:class="{ 'form-control': true}" name="location">
                                        <option  v-bind:value="item.LocationId">{{item.LocationName}}</option>
                                    </select>
                                </template>
                            </td>
                        </tr>
                    </template>
                    <template v-else>
                        <tr class="showassetcataloguegroup" :class="{'insertedrow bg-gold':item.AssetCatalogueGroup.length>0}">
                            <td :style='containercolor' class="textaligncenter align-middle text-center">
                                <span v-if="item.AssetCatalogueGroup.length>0" class='togglearrow arrowwhitecollapse_roles fa fa-caret-right fa-2x showcatalog showassetcatalog'></span>
                                <span v-else class='showcatalog hidden showassetcatalog' ></span>
                            </td>
                            <td class="align-middle ad">{{ item.AssetCatalogName }}</td>
                            <td class="align-middle ad">
                                <template v-if="BarCodeData.length==1">
                                    <select  v-bind:class="{ 'form-control disabled': true}" name="location">
                                        <option  v-bind:value="item.LocationId">{{item.LocationName}}</option>
                                    </select>
                                </template>
                                <template v-else>
                                    <select  v-bind:class="{ 'form-control': true}" name="location">
                                        <option  v-bind:value="item.LocationId">{{item.LocationName}}</option>
                                    </select>
                                </template>
                            </td>
                        </tr>
                        <tr v-if="RequestFrom == 'assetassignment'">
                            <td colspan="8" class="textaligncenter align-middle no_records">{{('NoItemAvailable')}}</td>
                        </tr>
                    </template>
                </tbody>
            </table>
        </div>
        </div>
</template>
<script>
export default {
    props:{
        BarCodeData:{
            type:Array,
            required:true
        },
        RequestFrom:{
            type:String,
            required:true
        }
    },
    data(){
        return{
            containercolor:''
        }
    },
    created:function(){         
        this.BarCodeData.forEach((item)=>{
            if (item.IsContainer == 1){
                this.containercolor = "border-left: 6px solid #227003 !important;";
            }
        })
    }
}
</script>